  position: relative;
  font-size: 13px;
}
.nav-item:hover {
  background: rgba(255, 255, 255, 0.05);
  color: var(--sidebar-active);
}
.nav-item.active {
  background: var(--primary);
  color: white;
  box-shadow: 0 4px 12px rgba(37, 99, 235, 0.3);
}
.nav-badge {
  position: absolute;
  right: 10px;
  background: #ef4444;
  color: white;
  font-size: 10px;
  padding: 2px 6px;
  border-radius: 10px;
  font-weight: bold;
}
.user-profile {
  padding: 16px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  display: flex;
  gap: 12px;
  align-items: center;
  background: rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: 0.2s;
}
.user-profile:hover {
  background: rgba(255, 255, 255, 0.05);
}
.avatar {
  width: 32px;
  height: 32px;
  background: linear-gradient(135deg, #6366f1, #a855f7);
  border-radius: 50%;
  display: grid;
  place-items: center;
  font-weight: bold;
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.2);
  flex-shrink: 0;
  background-size: cover;
  background-position: center;
}
.topbar {
  height: 60px;
  background: var(--bg-card);
  border-bottom: 1px solid var(--border);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 24px;
  flex-shrink: 0;
  z-index: 10;
}
.content {
  flex: 1;
  overflow-y: auto;
  padding: 24px;
  position: relative;
  scroll-behavior: smooth;
}
.card {
  background: var(--bg-card);
  border: 1px solid var(--border);
  border-radius: 10px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  margin-bottom: 20px;
  overflow: hidden;
  transition: transform 0.2s;
}
.card-head {
  padding: 14px 20px;
  border-bottom: 1px solid var(--border);
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(0, 0, 0, 0.01);
}
.card-body {
  padding: 20px;
}
.btn {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 8px 14px;
  border-radius: 6px;
  font-weight: 500;
  cursor: pointer;
  font-size: 13px;
  border: 1px solid transparent;
  transition: 0.2s;
}
.btn-primary {
  background: var(--primary);
  color: white;
  box-shadow: 0 2px 4px rgba(37, 99, 235, 0.2);
}
.btn-primary:hover {
  background: var(--primary-dark);
  transform: translateY(-1px);
}
.btn-ghost {
  background: transparent;
  color: var(--text-muted);
}
.btn-ghost:hover {
  background: var(--bg-body);
  color: var(--text-main);
}
.btn-outline {
  border: 1px solid var(--border);
  background: transparent;
  color: var(--text-main);
}
.btn-outline:hover {
  border-color: var(--text-muted);
}
.btn-icon {
  padding: 8px;
  border-radius: 50%;
}
input,
select,
textarea {
  padding: 8px 12px;
  border-radius: 6px;
  border: 1px solid var(--border);
  background: var(--bg-card);
  color: var(--text-main);
  font-size: 13px;
  width: 100%;
  transition: border-color 0.2s;
}
input:focus,
select:focus {
  border-color: var(--primary);
  outline: 3px solid var(--primary-light);
}
.role-btn {
  display: flex;
  align-items: center;
  gap: 12px;
  width: 100%;
  padding: 16px;
  border: 1px solid var(--border);
  border-radius: 12px;
  background: white;
  cursor: pointer;
  transition: 0.2s;
  text-align: left;
}
.role-btn:hover {
  border-color: var(--primary);
  background: var(--primary-light);
}
.badge {
  padding: 3px 8px;
  border-radius: 99px;
  font-size: 11px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}
.bg-success {
  background: var(--success-bg);
  color: var(--success);
}
.bg-warning {
  background: var(--warning-bg);
  color: var(--warning);
}
.bg-danger {
  background: var(--danger-bg);
  color: var(--danger);
}
.bg-primary {
  background: var(--primary-light);
  color: var(--primary);
}
.bg-super {
  background: #312e81;
  color: #a5b4fc;
}
.table-container {
  overflow-x: auto;
  width: 100%;
}
table {
  width: 100%;
  border-collapse: collapse;
  font-size: 13px;
  white-space: nowrap;
}
th {
  text-align: left;
  padding: 12px 16px;
  background: rgba(0, 0, 0, 0.02);
  border-bottom: 1px solid var(--border);
  color: var(--text-muted);
  font-weight: 600;
  text-transform: uppercase;
  font-size: 11px;
  letter-spacing: 0.5px;
}
td {
  padding: 12px 16px;
  border-bottom: 1px solid var(--border);
  vertical-align: middle;
}
tr:hover td {
  background: rgba(0, 0, 0, 0.01);
}
.grid-4 {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
}
.chat-layout {
  display: flex;
  height: calc(100vh - 120px);
  background: var(--bg-card);
  border-radius: 12px;
  border: 1px solid var(--border);
  overflow: hidden;
}
.chat-list {
  width: 280px;
  border-right: 1px solid var(--border);
  display: flex;
  flex-direction: column;
  background: var(--bg-body);
}
.chat-main {
  flex: 1;
  display: flex;
  flex-direction: column;
  background: var(--bg-card);
}
.chat-item {
  padding: 12px 16px;
  border-bottom: 1px solid var(--border);
  cursor: pointer;
  transition: 0.2s;
  display: flex;
  gap: 10px;
  align-items: center;
}
.chat-item:hover {
  background: rgba(0, 0, 0, 0.03);
}
.chat-item.active {
  background: var(--primary-light);
  border-left: 3px solid var(--primary);
}
.messages-area {
  flex: 1;
  padding: 20px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
  background: var(--bg-body);
}
.message {
  max-width: 75%;
  padding: 8px 12px;
  border-radius: 8px;
  font-size: 13px;
  line-height: 1.4;
  position: relative;
  animation: fadeIn 0.2s;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
}
.msg-self {
  align-self: flex-end;
  background: var(--chat-bg-self);
  color: var(--text-main);
}
.msg-other {
  align-self: flex-start;
  background: var(--bg-card);
  border: 1px solid var(--border);
  color: var(--text-main);
}
.msg-system {
  align-self: center;
  font-size: 11px;
  color: var(--text-muted);
  background: rgba(0, 0, 0, 0.05);
  padding: 4px 12px;
  border-radius: 20px;
  margin: 8px 0;
  border: none;
  box-shadow: none;
}
.chat-input {
  padding: 12px;
  background: var(--bg-card);
  border-top: 1px solid var(--border);
  display: flex;
  gap: 10px;
  align-items: center;
}
.slide-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.3);
  z-index: 90;
  display: none;
}
.slide-over {
  position: fixed;
  top: 0;
  right: 0;
  width: 600px;
  height: 100%;
  background: var(--bg-card);
  border-left: 1px solid var(--border);
  transform: translateX(100%);
  transition: transform 0.3s cubic-bezier(0.16, 1, 0.3, 1);
  z-index: 100;
  display: flex;
  flex-direction: column;
  box-shadow: -10px 0 30px rgba(0, 0, 0, 0.1);
}
.slide-over.open {
  transform: translateX(0);
}
.tabs {
  display: flex;
  border-bottom: 1px solid var(--border);
  background: var(--bg-body);
}
.tab {
  flex: 1;
  padding: 12px;
  text-align: center;
  cursor: pointer;
  font-weight: 500;
  color: var(--text-muted);
  border-bottom: 2px solid transparent;
  transition: 0.2s;
  font-size: 13px;
}
.tab.active {
  color: var(--primary);
  border-bottom-color: var(--primary);
  background: var(--bg-card);
}
.tab-pane {
  flex: 1;
  overflow-y: auto;
  padding: 24px;
  display: none;
}
.tab-pane.active {
  display: block;
}
.chat-pane {
  display: none;
  flex-direction: column;
  padding: 0;
  height: 100%;
}
.chat-pane.active {
  display: flex;
}
.profile-avatar-edit {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: #e2e8f0;
  margin: 0 auto 15px;
  position: relative;
  overflow: hidden;
  display: grid;
  place-items: center;
  cursor: pointer;
  font-size: 32px;
  font-weight: bold;
  color: var(--text-muted);
  transition: 0.2s;
  border: 4px solid var(--bg-body);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}
.profile-avatar-edit:hover {
  border-color: var(--primary);
  color: var(--primary);
}
.profile-avatar-edit::after {
  content: 'CAMBIAR';
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
  background: rgba(0, 0, 0, 0.6);
  color: white;
  font-size: 9px;
  padding: 4px 0;
}
.doc-viewer-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(15, 23, 42, 0.9);
  z-index: 200;
  display: none;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(4px);
}
.doc-viewer-window {
  width: 90%;
  height: 95%;
  background: #e2e8f0;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.sim-pdf-page {
  width: 800px;
  min-height: 1100px;
  background: white;
  padding: 50px;
  margin: 20px auto;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
  font-family: 'Times New Roman', serif;
  color: black;
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(5px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.scrollbar-hide {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.scrollbar-hide::-webkit-scrollbar {
  display: none;
}
@media (max-width: 1023px) {
  .topbar {
    padding: 0 12px;
    height: 56px;
  }
  .content {
    padding: 16px;
  }
  .card-head {
    padding: 12px 16px;
    flex-wrap: wrap;
    gap: 8px;
  }
  .card-body {
    padding: 16px;
  }
  .sim-pdf-page {
    width: 100%;
    min-height: auto;
    padding: 20px;
  }
  .doc-viewer-window {
    width: 100%;
    height: 100%;
    border-radius: 0;
  }
}
@media (max-width: 640px) {
  .content {
    padding: 12px;
  }
  .card {
    border-radius: 8px;
    margin-bottom: 12px;
  }
  .card-head {
    padding: 10px 12px;
  }
  .card-body {
    padding: 12px;
  }
  .btn {
    padding: 6px 12px;
    font-size: 12px;
  }
  .topbar h2 {
    font-size: 14px;
  }
  table {
    display: block;
    overflow-x: auto;
    white-space: nowrap;
  }
  .sim-pdf-page {
    padding: 12px;
    font-size: 14px;
  }
}
